import {AContainer} from "./abstract/a-container";
import {IChart} from "../chart/interface/i-chart";
import {IWidget} from "../widget/interface/i-widget";
import {ICustomComponent} from "../core/interface/i-custom-component";
import {AVisualComponent} from "../component/abstract/a-visual-component";
import {Panel} from "./panel";
import {Form} from "./form";
import {IBlockContainer} from "./interface/i-block-container";
import {IRenderable} from "../core/interface/i-renderable";
import jquery = require("jquery");

/**
 * <img src="../../doc_assets/container_box_example_1.png">
 * <img src="../../doc_assets/container_box_example_2.png">
 * 
 * Container sem bordas que acomoda os componentes adicionados de forma horizontal.
 * A largura do espaço horizontal ocupado por cada componente adicionado é definido pela propriedade
 * [`size`]{@link AVisualComponent.setSize} do mesmo.
 *  
 * @memberof module:mvcomponents/container
 *
 * @example <caption>Neste exemplo os botões adicionados ficarão flutuando à esquerda, um ao lado do outro:</caption>
 * 
 * let boxContainer:Box = new Box();
 * boxContainer.append(new Button("Botão 1"));
 * boxContainer.append(new Button("Botão 2")); 
 *
 * @example <caption>Neste outro exemplo, os botões ocuparão proporcionalmente o espaço:</caption>
 * 
 * let boxContainer:Box = new Box();
 * let btnA:Button = new Button("Botão 1");
 * btnA.setSize(6);
 * let btnB:Button = new Button("Botão 2");
 * btnB.setSize(6);
 * boxContainer.append(btnA);
 * boxContainer.append(btnB);
 * 
 */
export class Box extends AContainer<IRenderable>{
	constructor(){
		super("div","");
		jquery(this.element).addClass("Box");
	}
}
